*{
  font-family: 'Roboto Condensed', sans-serif;
}
body{
  padding: 0;
  margin: 0;
}
.app{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  padding: 0;
  margin: 0;

}
.contador {
  width: 40%;
  text-align: center;
  margin: 0;

}
.contador button{
  display: block;
  width: 100%;
  margin: 1em 0;
  border: none;
  border-radius: 1em;
  padding: .5em 0;
  background: #ccc;
  outline: none;
  cursor: pointer;
}
.contador button:hover{
  background: #bbb;
  font-weight: bold;
}
.alumnos{
  width: 100%;
  text-align: center;
  padding: 0;
  margin: 0;
}
.alumnos input, .alumnos button{
  margin: 0 auto;
  display: block;
  width: 50%;
  padding: .5em 0;
  border: none;
  border-radius: 1em;
  text-align: center;
  outline: none;
  font-size: 15px;
}
.alumnos input{
 border: .5px solid #ccc;
 margin-bottom: .5em; 
}
.alumnos input[type="submit"]{
  background: #ccc;
}
.alumnos input[type="submit"]:hover{
  background: #bbb;
  font-weight: bold;
}
.datos{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 1em;
  margin: 1em;
}
.datos div{
  background: #ccc;
  border-radius: 1em;
  padding-top: 1em;
}
.datos div span{
  display: block;
}
.name{
  font-size: 23px;
}
header {
  text-align: center;
  display: block;
  width: 100%;
  border-bottom: 1px solid #ccc;
}
a {
  display: inline-block;
  text-decoration: none;
  padding: 1em;
  color: #888;
  margin-top: .5em;
}
a:hover{
  font-weight: bold;
}
.active {
  color: rgb(40, 76, 80);
  font-weight: bolder;
}
.alumno-data{
  background-color: #eee;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;
}
.alumno-data .alumno{
  display: block;
  border: .5px solid rgba(0,0,0,.2);
  margin-top: 1em;
  border-radius: 1em;
  overflow: hidden;
}
.alumno h3 {
  text-align: center;
}
.alumno p{
  text-align: center;
  background-color: #ddd;
  margin: 0;
  padding: .5em;
  border-top: .5px solid rgba(0,0,0,.2); 
}

@media (max-width: 650px) {
  .app{
    justify-content: stretch;
  }
  .contador{
    width: 70%;
  }
  .datos{
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-gap: .5em;
    margin: .5em;
  }
  .alumnos input, .alumnos button{
    width: 70%;
  }

}
@media (max-width: 400px) {
  .contador{
    width: 80%;
  }
  .alumnos input, .alumnos button{
    width: 90%;
  }
  .datos{
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-gap: .5em;
    margin: .5em;
  }
}